<template>
  <div>
    <div class="list-item">
      <img :src="itemData.poster" :alt="itemData.name" />
      <div class="info">
        <div class="info-header">
          {{ itemData.name }} <span class="type">{{ itemData.filmType.name }}</span>
        </div>
        <div class="info-main">
          观众评分 <span class="orange">{{ itemData.grade || '暂无' }}</span>
        </div>
        <div class="info-main">主演：{{ getActors }}</div>
        <div class="info-main">{{ itemData.nation }} | {{ itemData.runtime }}分钟</div>
      </div>
      <div class="btn">购票</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListItem',
  props: {
    itemData: {
      type: Object,
      default() {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  computed: {
    getActors() {
      return this.itemData.actors.map((item) => item.name).join(' ')
    },
  },
}
</script>

